body{
  background-color: rgb(250, 250, 250);
}
.container{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.logo{
  height: 90px;
  width: 180px;
  margin-top: 30px;
}
.navbar{
  display: flex;
  flex-direction: row;
}


@media(max-width:770px){
  .container{
    margin: 0 10%;
  }

  .image{
    width: 100%;
  }
}
